              })
              if (label === 'Art√≠culos:') {
                vueThis.dialogArticulosLabels = vueThis.articulos.filter(x => labeMap.includes(x.organizacion)).sort((x, y) => x.organizacion_id - y.organizacion_id)
                vueThis.openDialog('dialogArticulos')
              }
              if (label === 'Partes') {
                vueThis.dialogPartesDeLibroLabels = vueThis.partesDeLibro.filter(x => labeMap.includes(x.organizacion)).sort((x, y) => x.organizacion_id - y.organizacion_id)
                vueThis.openDialog('dialogPartesDeLibro')
              }
              if (label === 'Libros:') {
                vueThis.dialogLibrosLabels = vueThis.libros.filter(x => labeMap.includes(x.organizacion)).sort((x, y) => x.organizacion_id - y.organizacion_id)
                vueThis.openDialog('dialogLibros')
              }
              if (label === 'Trabajos') {
                vueThis.dialogTrabajosEnEventosCTPublicadosLabels = vueThis.trabajosEnEventosCTPublicados.filter(x => labeMap.includes(x.organizacion)).sort((x, y) => x.organizacion_id - y.organizacion_id)
                vueThis.openDialog('dialogTrabajosEnEventosCTPublicados')
              }
              if (label === 'Demas') {
                vueThis.dialogDemasProduccionesCTLabels = vueThis.demasProduccionesCT.filter(x => labeMap.includes(x.organizacion)).sort((x, y) => x.organizacion_id - y.organizacion_id)
                vueThis.openDialog('dialogDemasProduccionesCT')
              }
            }
          }
        }
      })
      let ctx2 = document.getElementById('personalChart')
      this.personalChart = new Chart(ctx2, {
        type: 'bar',
        data: {
          labels: [`Personal Memoria: ${this.personalEnMemoria.filter(x => labeMap.includes(x.organizacion)).length}`,
            `Personal Relevante: ${this.listadoPersonalRelevante.filter(x => labeMap.includes(x.organizacion)).length}`,
            `Personal Productivo: ${this.listadoPersonalProductivo.filter(x => labeMap.includes(x.organizacion)).length}`,
            `Personal Nulo: ${this.listadoPersonalNulo.filter(x => labeMap.includes(x.organizacion)).length}`],
          datasets: this.datasetPersonal.filter(x => this.$store.state.curusr.ua.includes(x.label))
        },
        options: {
          scales: {
            yAxes: [{
              stacked: true
            }]
          },
          onClick: function (ev, legend) {
            if (legend.length > 0) {
              let vueThis = window.$vue.$children[0].$children[1]
              let label = legend[0]._model.label.split(' ')[1].slice(0, -1)
              let labels = legend.map(x => x._model.datasetLabel)
              let labeMap = labels.map(label => {
                if (label === 'Instituto') return 'Instituto'
                if (label === 'Escuela') return 'Escuela'
                if (label === 'Facultad') return 'Facultad'
              })
              if (label === 'Memoria') {
                vueThis.dialogPersonalEnMemoriaLabels = vueThis.personalEnMemoria.filter(x => labeMap.includes(x.organizacion)).sort((x, y) => x.organizacion_id - y.organizacion_id)
                vueThis.openDialog('dialogPersonalEnMemoria')
              }
              if (label === 'Relevante') {
                vueThis.dialogPersonalRelevanteLabels = vueThis.listadoPersonalRelevante.filter(x => labeMap.includes(x.organizacion)).sort((x, y) => x.organizacion_id - y.organizacion_id)
                vueThis.openDialog('dialogPersonalRelevante')
              }
              if (label === 'Productivo') {
                vueThis.dialogPersonalProductivoLabels = vueThis.listadoPersonalProductivo.filter(x => labeMap.includes(x.organizacion)).sort((x, y) => x.organizacion_id - y.organizacion_id)
                vueThis.openDialog('dialogPersonalProductivo')
              }
              if (label === 'Nulo') {
                vueThis.dialogPersonalNuloLabels = vueThis.listadoPersonalNulo.filter(x => labeMap.includes(x.organizacion)).sort((x, y) => x.organizacion_id - y.organizacion_id)
                vueThis.openDialog('dialogPersonalNulo')
              }
            }
          }
        }
      })
    },
    openDialog (ref) {
      this.$refs[ref].open()
    },
    closeDialog (ref) {
      this.$refs[ref].close()
    }
  }
}
</script>



