<template>
<div id="nave">
<md-toolbar class="flex-nave">
  <form novalidate @submit.stop.prevent="submit" class="flex-nave-item" v-if="!this.$store.state.message">
    <md-input-container>
      <label>Ingrese usuario</label>
      <md-input
      type="text"
      class="form-control"
      v-model="username"
      ></md-input>
      <span><md-icon class="md-warn" v-show="this.$store.state.denied">warning</md-icon></span>
    </md-input-container>
  </form>

  <div class="flex-nave-item" v-if="this.$store.state.message"><md-icon>person</md-icon>
    <span>{{ this.$store.state.message }}</span>
  </div>

  <div class="flex-nave-item">
    <img class="unsamlogo" src="static/LogoSecInvHorizontalFondoTranspBlanco.gif" width="130">
  </div>
</md-toolbar>
</div>
</template>

<script>
export default {
  name: 'nave',
  data () {
    return {
      username: ''
    }
  },
  methods: {
    submit () {
      let curusr = this.$store.state.usuarios.find(x => x.usuario === this.username)
      if (curusr) {
        this.$store.state.message = curusr.nombre
        this.$store.state.denied = false
        this.$store.commit('setCurusr', curusr)
        this.$router.push('/medir')
      } else {
        this.$store.state.denied = true
      }
    }
  }
}
</script>



