import Vuex from 'vuex'
import Vue from 'vue'
Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    usuarios: [{
      usuario: 'usuario',
      nombre: 'Usuario Deprueba',
      ua: ['Instituto', 'Escuela', 'Facultad']
    }
    ],
    denied: false,
    message: '',
    curusr: ''
  },
  mutations: {
    setCurusr (state, usr) {
      state.curusr = usr
    }
  }
})



